<template>
    <div class="sneakGuestEdit">
        <div class='top'>
          <span class='fundInfo' @click='goback()'>排课管理</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <!-- 编辑状态 -->
        <div class="userInfo editUserInfo">
          <div class="userId">
            <span :class="form.teacherNo ? '' : 'yincan'">编号: {{form.teacherNo}}</span>
            <div class="operation">
              <div class="edit" v-if="editFlag" @click="edit()">
                  <i class="iconfont icon-btn_edit"></i>
                  <span>编辑</span>
              </div>
              <div class="cancle" v-if="!editFlag" @click="cancle()">
                取消
              </div> 
              <div class="sub" v-if="!editFlag" @click="submit()">
                提交
              </div>
            </div>
          </div>
          <div class="inputInfo">
              <div class="editBottom">
                <el-form ref="form" :model="form" :rules='rules'>
                  <div class="left">
                  	<el-form-item label="专业：" class='formNew noback birthday' prop='professional'>
                      <el-select v-model="form.professional" placeholder="请选择" @change="changePro($event)">
                        <el-option
                          v-for="item in professionOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="课程：" class='formNew noback birthday' prop='courseId'>
                      <el-select v-model="form.courseId" placeholder="请选择">
                        <el-option
                          v-for="item in courseOptions"
                          :key="item.id"
                          :label="item.courseName"
                          :value="item.id"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="指导教师：" class='formNew noback birthday' prop='teacherId'>
                      <el-select v-model="form.teacherId" placeholder="请选择">
                        <el-option
                          v-for="item in teacherOptions"
                          :key="item.id"
                          :label="item.name"
                          :value="item.id"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </div>
                  <div class="center">
                  	<el-form-item label="星期：" class='formNew noback birthday' prop='week'>
                      <el-select v-model="form.week" placeholder="请选择">
                        <el-option
                          v-for="item in weekOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="上课时间：" class='formNew noback birthday' prop='time'>
                      <el-select v-model="form.time" placeholder="请选择">
                        <el-option
                          v-for="item in timeOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="开课状态：" class='formNew noback birthday' prop='isactive'>
                      <el-select v-model="form.isActive" placeholder="请选择">
                        <el-option
                          v-for="item in isactiveOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </div>
                  <div class="right">
                    
                  </div>
                 </el-form> 
              </div>
          </div>
        </div>
    </div>
</template>
<script>
import Vue from 'vue'
import {addScheduleCourse,upadteScheduleCourse,queryTeacher, queryCourse} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage, timeformer} from '../../utils/com.js'
import { fundFormat } from '../../filter/index.js';
export default {
  data () {
    var checkContact = (rule, value, callback) => {
      if (!value) {
        callback(new Error('手机号不能为空'));
      } else {
        let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
        if (!FormatReg.test(value)) {
            callback ('请填写正确的手机号码')
        } else {
          callback()
        }
      }
    };
    var checkEmail = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请填写邮箱'));
      } else {
        let FormatReg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
        if (!FormatReg.test(value)) {
            callback ('请填写正确的邮箱')
        } else {
          callback()
        }
      }
    };
    return {
      form: {
        courseId: '',
        professional: '',
        teacherId: '',
        week: '', 
        time: '',
        isActive: ''
      },
      rules: {
        score: [{required: true, message: '编号不能为空', trigger: 'blur'}],
        personLimit: [{required: true, message: '人数不能为空', trigger: 'blur'}],
        courseId: [{required: true, message: '课程名称不能为空', trigger: 'blur'}],
        week: [{required: true, message: '上课时间不能为空', trigger: ['blur', 'change'] }],
        time: [{required: true, message: '上课时间不能为空', trigger: ['blur', 'change'] }],
        professional: [{required: true, message: '专业不能为空', trigger: ['blur', 'change'] }],
        teacherId: [{required: true, message: '指导教师不能为空', trigger: ['blur', 'change'] }]
      },
      weekOptions: [
        {value: 1, label: '周一'},
        {value: 2, label: '周二'},
        {value: 3, label: '周三'},
        {value: 4, label: '周四'},
        {value: 5, label: '周五'},
        {value: 6, label: '周六'},
        {value: 7, label: '周日'}
      ],
      timeOptions: [
        {value: '8:00~9:40', label: '8:00~9:40'},
        {value: '10:00~11:40', label: '10:00~11:40'},
        {value: '14:00~15:40', label: '14:00~15:40'},
        {value: '16:00~17:40', label: '16:00~17:40'}
      ],
      isactiveOptions: [
        {value: 1, label: '是'},
        {value: 2, label: '否'}
      ],
      teacherOptions: [],
      courseOptions: [],
      professionOptions: [
      	{value: 1, label: '计算机专业'}, //Computer 
        {value: 2, label: '英文专业'}, //English  
        {value: 3, label: '中文专业'}, //Chinese 	
        {value: 4, label: '数学专业'}, //Math 			
        {value: 5, label: '医学专业'}  //Doctor		
      ],
      editFlag: true,
      text: '排课信息',
      errorTips: '',
      errorTips2: '',
      id: ''
    }
  },
  watch: {
  },
  created () {
    if (this.$route.query.btnType == 'edit') {
      this.editFlag = false
      this.text = '修改排课信息'
      let that = this
      console.log(getSessionStorage('detailInfo'))
      let detailInfo = getSessionStorage('detailInfo')
      this.id = detailInfo.id
      this.form.courseId = detailInfo.courseId
      this.form.professional = detailInfo.course.professional
      this.form.teacherId = detailInfo.teacherId
      this.form.week = detailInfo.week
      this.form.time = detailInfo.time
      this.form.isActive = detailInfo.isActive
      this.getTeacherList(this.form.professional)
	  this.getCourseList(this.form.professional)
    }
    if (this.$route.query.btnType == 'add') {
      this.editFlag = false
      this.text = '添加排课信息'
    }
  },
  methods: {
  	// 选择专业
  	changePro (id) {
  		console.log(id)
	    this.getTeacherList(id)
	    this.getCourseList(id)
	    Vue.set(this.form, 'teacher', '')
	    Vue.set(this.form, 'courseId', '')
  	},
  	// 获取教师
  	getTeacherList (id) {
  		let that = this
  		queryTeacher({Professional: id}).then(function (res){
  			if (res.code == 0) {
  				var list = res.data.data
  				for (var i=0; i<list.length; i++) {
    				for (var key in list[i].users) {
    					if (key != 'id') {
    						list[i][key] = list[i].users[key]
    					}
    				}
    			}
  				that.teacherOptions = list
            } else {
                that.$message({
                  showClose: true,
                  message: res.message,
                  type: 'warning'
                })
            }  
  		})
  	},
  	// 获取课程
  	getCourseList (id) {
  		let that = this
  		queryCourse({Professional: id}).then(function (res){
  			if (res.code == 0) {
  				that.courseOptions = res.data.data
            } else {
                that.$message({
                  showClose: true,
                  message: res.message,
                  type: 'warning'
                })
            } 
  		})
  	},
    goback () {
      this.$router.push('courseList')
    },
    // 编辑
    edit () {
      this.editFlag = false
    },
    // 取消
    cancle () {
      this.$router.push('selectCourseList')
    },
    // 保存
    async submit () {
      let that = this
      if (this.errorTips || this.errorTips2) {
        return
      }
      this.$refs['form'].validate((valid) => {
        if (valid) {
        	console.log(that.form)
          if (this.$route.query.btnType == 'add') {
            addScheduleCourse(that.form).then(function (res) {
              if (res.code == 0) {
                that.$message({
                  showClose: true,
                  message: '添加成功',
                  type: 'success'
                })
                that.$router.push({name: 'selectCourseList'})
              } else {
                that.$message({
                  showClose: true,
                  message: res.message,
                  type: 'warning'
                })
              }  
            })
          } else {
            upadteScheduleCourse(that.id, that.form).then(function (res) {
              if (res.code == 0) {
                that.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
                })
                that.$router.push({name: 'selectCourseList'})
              } else {
                that.$message({
                  showClose: true,
                  message: res.message,
                  type: 'warning'
                })
              }  
            })
          }
        }
      })
      
    },
    // 图片上传
    async fileSelected (obj) {
      let formData = new FormData()
      let file = document.getElementById('fileToUpload').files[0]
      if (!file) return
      formData.append('image', file)
      const res = await upload(formData)
      if (isApiResponseOk(res)) {
        this.form.headImage = res.data
        this.$message({
          showClose: true,
          message: '恭喜您,上传成功',
          type: 'success'
        })
      } else {
        this.$message({
          message: '上传失败，请重新上传',
          type: 'warning'
        })
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.sneakGuestEdit
    .el-form
        .el-form-item
          background: #FBFBFC;
          border: 1px solid #EAECEF;
          margin: 0.2rem 0;
          padding-left: 0.2rem;
          position: relative;
          height: 0.5rem;
          // overflow: hidden;
    .btn
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        justify-content: center;
        .submit, .cancle
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            margin-top: .3rem;
            margin-bottom: .5rem;
            cursor: pointer;
        .cancle
          background: #C0C4CC;
          margin-right: 0.3rem;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-size: .24rem;
            font-family: PingFangSC-Semibold;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
    .editUserInfo
      padding-bottom: 0.5rem!important;
      .inputInfo
        .editTop
          width: 100%;
          height: 1.2rem;
          display: flex;
          align-items: center;
          justify-content: center;
          .headerImg
            width: 0.6rem;
            height: 0.6rem;
            border-radius: 50%;
            cursor: pointer;
            position: absolute;
        .editBottom
          width: 100%;
          .el-form
            display: flex;
            justify-content: center;
            .left,.center,.right
              display: flex;
              flex-direction: column;
              align-items: center;
            .center
              margin-left: 1.6rem;  
              margin-right: 1.6rem;
            .el-form-item
              margin-bottom: 0.12rem;
            .formNew
              width: 3.8rem;
              height: 0.5rem;
              display: flex;
              align-items: center;
              padding: 0 0.2rem;
              box-sizing: border-box;
              background: #FBFBFC;
              border: 1px solid #EAECEF;
              border-radius: 2px;
            .noback.formNew
              background: #fff;
            formNew.formArea 
              align-items: normal;        
    .userInfo
      width: 100%;
      padding-left: 0.14rem;
      padding-bottom: 0.2rem;
      background: #fff;
      box-sizing: border-box;
      .userId
        width: 100%;
        height: 0.6rem;
        line-height: 0.6rem;
        padding-left: 0.2rem;
        box-sizing: border-box;
        font-family: BebasNeueCyrillic;
        font-size: .2rem;
        color: #C0C4CC;
        border-bottom: 1px solid #EAECEF;
        margin: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .operation
          display: flex;
          justify-content: flex-end;
          align-items: center;
          .cancle,.sub
            width: 1rem;
            height: 0.36rem;
            line-height: 0.36rem;
            text-align: center;
            border-radius: 0.04rem;
            cursor: pointer;
            font-size: 0.16rem;
          .cancle
            border: 1px solid #E9ECEF;
            color: #666;
          .sub
            background-image: linear-gradient(-138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
            color: #fff;
            margin-left: 0.12rem;
            margin-right: 0.36rem;
          .edit
            cursor: pointer;
            display: flex;
            width: 1rem;
            height: 0.36rem;
            justify-content: center;
            align-items: center;
            border: 1px solid #E9ECEF;
            border-radius: 0.04rem;
            color: #20C6BA;
            font-weight: 800;
            font-size: 0.16rem;
            margin-right: 0.2rem;
            .icon-btn_edit
              margin-right: 0.1rem;
    .infoDetail
      width: 100%;
      height: 1.69rem;
      .userHeadImg
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        float: left;
        margin-left: 1rem;
        margin-right: 1rem;
        .headImg
          width: 0.6rem;
          height: 0.6rem;
          border-radius: 50%;
          margin-bottom: 0.16rem;
        .username
          color: #263238;
          font-size: .16rem;
          .icon-icon_boy,
            color: #20C6BA;
            margin-left: .1rem;
          .icon-icon_girl
            color: #fc63b7;
            margin-left: .1rem;
      .infoList
        display: flex;
        flex-direction: column;
        float: left;
        .noflex.infoItem
          align-items: normal;
          .itemName
            width: 0.6rem;
        .infoItem
          display: flex;
          align-items: center;
          margin-bottom: 0.2rem;
          width: 3.2rem;
          .itemName
            width: 1rem;
            font-family: PingFangSC-Regular;
            font-size: 0.14rem;
            color: #8597A1;
          .itemValue
            flex: 1;
            font-family: PingFangSC-Semibold;
            font-size: 0.14rem;
            color: #263238;
          .itemValueList
            flex: 1;
            align-items: center;
            span
              margin-right: 0.1rem;
              font-size: 0.14rem;
              color: #263238;        
</style>
<style>
	.sneakGuestEdit .textareaNew.formNew .el-textarea__inner{
		height: 1.2rem!important;
		text-align: right;
    padding: 0;
    line-height: 0.36rem;
	}
	.sneakGuestEdit .textareaNew.formNew{
		height: 1.2rem!important;
		align-items: normal!important;
	}
.sneakGuestEdit .editBottom .el-form-item__label{
  width: 1rem;
  white-space: nowrap;
  text-align: left;
}
.sneakGuestEdit .editBottom .formNew.formArea .el-form-item__label{
  width: 0.6rem;
  white-space: nowrap;
  text-align: left;
}
.sneakGuestEdit .yincan{
  visibility: hidden;
}
.sneakGuestEdit #fileToUpload{
    opacity: 0.01;
    width: 1rem;
    height: 100%;
    z-index: 12;
    outline: none;
    cursor: pointer;
}
.sneakGuestEdit .editBottom .el-form-item__content{
  flex: 1;
  line-height: 0.5rem; 
}
.sneakGuestEdit  .el-date-editor.el-input, .sneakGuestEdit  .el-date-editor.el-input__inner{
  width: 100%;
}
.sneakGuestEdit .editBottom .el-form-item__content .el-input__inner{
  border: none;
  /* height: 0.48rem; */
  /* line-height: 0.48rem; */
  background: #FBFBFC;
  text-align: right;
  padding: 0;
  font-size: 0.16rem;
  color: #263238;
  height: 0.3rem;
}
.sneakGuestEdit .editBottom .noback.formNew .el-input__inner{
  background: #fff;
}
.sneakGuestEdit .formNew  .el-form-item__error{
  right: 0;
  text-align: right;
  position: absolute;
  top: 100%;
}
.el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:before{
  margin: 0;
}
.sneakGuestEdit .el-form-item__label:before{
  content: ''!important;
  margin: 0;
}
.sneakGuestEdit  .nomust .el-form-item__label:after{
  content: '';
}
.sneakGuestEdit .el-form-item__label:after{
  content: '*';
  color: #f56c6c;
  margin-right: 4px;
  position: relative;
  top: 2px;
  left: 0px;
}
.sneakGuestEdit .formNew.errorTips{
  position: relative;
}
.sneakGuestEdit .formNew.errorTips .error{
  position: absolute;
  bottom: -0.4rem;
  color: #f56c6c;
  right: 0rem;
}
.sneakGuestEdit .formNew.formArea .el-form-item__label{
  font-size: 14px;
  color: #8597A1!important;
  line-height: 0.4rem;
}
.sneakGuestEdit .formNew.formArea{
  height: 1.2rem!important;
}
.sneakGuestEdit .el-select{
  width: 100%;
}
.sneakGuestEdit .editBottom .birthday .el-form-item__content .el-input__inner{
  padding: 0 0.4rem;
  padding-left: 0;
  height: 0.4rem!important;
}
.sneakGuestEdit .el-textarea__inner{
  font-size: 0.16rem;
  color: #263238;
  text-align: left;
  line-height: 0.22rem;
  border: none;
  background: none;
  resize:none ;
  font-family: none;
  text-align: left;
}
.sneakGuestEdit .editUserInfo .inputInfo .editBottom .el-form .formNew.formArea{
  align-items: normal;
}
.sneakGuestEdit ::-webkit-scrollbar{
  width: 4px;  
  height: 16px;  
  background-color: #dcdfe6;
}
.sneakGuestEdit ::-webkit-track{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  border-radius: 4px;  
  background-color: #fff;  
}
.sneakGuestEdit ::-webkit-thumb{
  border-radius: 10px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color: #fff;
}
</style>




